* {
  box-sizing: border-box;
}

body {
}


.header{
    margin: 0;
    display: flex;
    padding: 0px 40px;
    box-shadow: 0px 3px 7.5px;
    align-items: center;
}

.headerlogo{
    width: 75px;
    height: 75px;
}

.storename{
    margin: 30px;
    font-size: 50px;
}

.navmenu{
    display: flex;
    margin-left: 700px;
    font-size: 20px;
}

.navmenu > .navlink {
    margin-left: 20px;
}

.cart{
    font-size: 25px;
    margin-left: auto;
    padding: 0px 20px;
}

.total{
    font-size: 25px;
}


.log-form {
  width: 40%;
  min-width: 320px;
  max-width: 475px;
  background: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%,-50%);
transform: translate(-50%,-50%);
  border-style: solid;
  /* box-shadow: 0px 2px 5px rgba(0,0,0,.25); */
}
  
  .form {
    display: block;
    width: 100%;
    padding: 1.5em;
  }
  
  .loginheader {
    width: 100%;
    color: black;
    font-family: 'open sans condensed';
    font-size: 1.35em;
    display: block;
    width: 100%;
    text-transform: uppercase;
    padding: .75em 1em .75em 1.5em;
    /* box-shadow:inset 0px 1px 1px; */
    border-style: dotted;
    margin: 0;
    font-weight: 200;
    margin-top: 25px;
  }
  
  input {
    display: block;
    margin: auto auto;
    width: 100%;
    margin-bottom: 2em;
    padding: .5em 0;
    border: none;
    border-bottom: 1px solid #eaeaea;
    padding-bottom: 1.25em;
    color: #757575;
    box-shadow: inset;

  }
  
  .btn {
    display: inline-block;
    padding: .5em 2em;
    color: rgb(0, 0, 0);
    margin-right: .5em;
    box-shadow: inset 0px 1px 0px fadeout(white, 80%); 

  }

  .logstatus{

  }
  

  

.Body{
}

.MainBody{
    margin-left: 100px;
    margin-top: 50px;
  }

.column{
    float: left;
    width: 50%;
}

.Instructions{
    font-size: 30px;
}

.item{
    display: flex;
}

.itemtext{
    margin-bottom: 0px;
    font-size: 25px;
}

.btns{
    display: inline-flex;
    position: absolute;
    flex-basis: 8rem;
    border-style: groove;
    border-radius: 12px;
    padding: 0px 15px;
    margin-left: 150px;
    align-items: center;
    margin-top: 20px;
}

.count{
    font-size: 20px;
    margin-left: 10px;
}

.pos{
    cursor: pointer;
    text-align: center;
    font-size: 15px;
}

.neg{
    cursor: pointer;
    text-align: center;
    font-size: 15px;
    margin-left: 10px;
}

.totaltext{
    position: absolute;
    margin-left: 270px;
    font-size: 20px;
    margin-bottom: 5px;
}



.aboutheader{
    margin-top: 30px; 
    font-size: 26px;
}

.footerbot{
    height: 35px; 
    position: fixed; 
    bottom:0%;
    width:100%; 
    background-color: #150552; 
    opacity: 1;
}
